.Game {
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 85.375rem;
  min-height: 35rem;
  height: 100vh;
  margin: 0 auto;
  padding: 1.95rem;
}

.Game > :nth-child(2) {
  flex-grow: 1;
}

@media (min-width: 500px) {
  .Game {
    min-height: 45rem;
    padding: 2.9rem 1.95rem 1.875rem;
  }
}